import config from '../config'
import Provider from '../models/Provider';
import { EventOdd, Result } from '../models/EventOdd';
import { OpenBet } from '../models/BetOrder';
import _ from 'lodash'
import moment from 'moment'
import axios from 'axios'
import xml2js from'xml2js-es6-promise'


const fetch_pickMon_result = async () => {
// 	get open bet unique id with pickMon
	const pickMonApi = await Provider.findOne({ provider: 'pickMon', activate: true })

	if(pickMonApi){

		const openBets = await OpenBet.find({})
		const openBetPickMonEvents = _.compact(_.uniqBy([].concat.apply([], openBets.map(openBet => {
			return openBet.eventOdds.map(event => {
				if(event.status === 'Pending' && event.source.provider === 'pickMon' && moment().isAfter(event.cutOffTime) ){
					event.sportLeague = `${event.sport.toLowerCase()}-${event.league.toLowerCase()}`
					return _.pick(event, ['eventOddId', 'sport', 'matchTime', 'details', 'league', 'region', 'status', 'team', 'source', 'oddType', 'sportLeague'])
				}
				return null
			})
		})), 'eventOddId'))

		const pickMonSportLeagues = openBetPickMonEvents.map(event => event.sportLeague).join(',')
		console.log('fetch pickmon result' + pickMonSportLeagues)

		if(!_.isEmpty(pickMonSportLeagues)){
			const response = await axios.get(`${pickMonApi.apiLink}uid=${config.pickMon_UID}&key=${config.pickMon_Key}&sports=${pickMonSportLeagues}&graded=1&full_call=${pickMonApi.option.resultFullCall}`)
			if(!_.isEmpty(response.data)){
				const pickMonData = await xml2js(response.data, {explicitArray: false})
				if(!_.isEmpty(pickMonData.lines) && !_.isEmpty(pickMonData.lines.game) && _.isFunction(pickMonData.lines.game.map)){
					await Promise.all(pickMonData.lines.game.map(async event => {

				 		if( event.line.score.winner ){ // only when event is officially finished // save result
				 			await Promise.all(openBetPickMonEvents.map(async openBetEvent =>{

				 				if(event.id === openBetEvent.source.id){
				 					console.log('checkhere pickmond result')
									const newResult = {
										eventResultId: openBetEvent.eventOddId,
										source: {
											provider: 'pickMon',
											id: openBetEvent.source.id,
										},
										sport: openBetEvent.sport,
										oddType: openBetEvent.oddType,
										league: openBetEvent.league,
										region: openBetEvent.region,
										details: openBetEvent.details,
										matchTime: openBetEvent.matchTime,
										team: openBetEvent.team,
										score:{
											homeScore: event.line.score.team1 || 0,
											awayScore: event.line.score.team2 || 0
										},
										expireAt: moment().add(3, 'd')
									}

				 					switch(event.void){
				 						case '0':
				 							newResult.status = 'Finished'
				 							break;
				 						// case '1':
				 						// 	newResult.status = 'Canceled'
				 						// 	newResult.note = 'pm showed void = 1'
				 						// 	break;
				 						default:
				 							newResult.status = 'Review'
				 							newResult.note = 'Server Reviewing... TBD, pm void ' + event.void
				 							return
				 					}

									const existedResult = await Result.findOne({ eventResultId: openBetEvent.eventOddId })
									if(_.isEmpty(existedResult)){
										await new Result(newResult).save()
										console.log('saved pickMon new result')
									}
				 				}
				 				return null

				 			}))
				 		}
				 		return null
					}))
				}
			}
		}else{
			console.log('use open bet event id to update pickmon result, but open bet empty')
		}

	}

}

export default fetch_pickMon_result

